angular.module("Skillopedia").controller("shoppingcartController", function($scope, $location, $window, $timeout, $route, orderServices, shoppingcartServices, errorServices, toastServices, localStorageService, config) {
	$scope.courses = [];
	$scope.page = {
		pn: 1,
		page_size: 1000,
		message: ""
	}
	$scope.loadMore = function() {
		if ($scope.no_more) {
			return;
		}
		toastServices.show();
		$scope.page.message = "...";
		shoppingcartServices.query($scope.page).then(function(data) {
			toastServices.hide();
			$scope.page.message = "";
			if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
				$scope.courses = $scope.courses.concat(data.Result.Carts.list);
				$scope.no_more = $scope.courses.length == data.Result.Carts.totalRow ? true : false;
			} else {
				errorServices.autoHide("");
			}
			if ($scope.no_more) {
				$scope.page.message = "" + $scope.courses.length + "";
			}
			$scope.courses.map(function(c) {
				// status 1:, status 2:;
				c.status == 1 ? c.selected = true : "";
				if (c.status == '2') {
					$scope.has_expired_cart = true;
				}
				return c;
			});
			$scope.page.pn++;
		})

	}
	$scope.loadMore();
	// open map
	$scope.open_map = function(course, e) {
		e.preventDefault();
		e.stopPropagation();
		$.magnificPopup.open({
			items: {
				src: "https://maps.google.com/maps?q=" + course.city + course.area + course.address
			},
			type: "iframe"
		});
	};
	// action
	$scope.toggle = function(course) {
		return course.selected = !course.selected;
	};
	var n = 1;
	$scope.check_all = function() {
		// status 1:, status 2:;
		n % 2 == 0 && $scope.courses.map(function(c) {
			c.status == '1' ? c.selected = true : "";
			return c;
		});
		n % 2 == 1 && $scope.courses.map(function(c) {
			c.status == '1' ? c.selected = true : "";
			return c;
		});
		n++;

		// $scope.get_total();
	}
	$scope.get_total = function() {
		var total = 0;
		$scope.shoppingcart_size = 0;
		angular.forEach($scope.courses, function(value, key) {
			if (value.selected) {
				$scope.shoppingcart_size++;
				total += value.total_session_rate
			}
		});
		return total.toFixed(2);
	};
	// 
	$scope.remove = function(course) {
		$scope.confirm.content = "";
		$scope.confirm.open();
		$scope.confirm.cancle_callback = function() {}
		$scope.confirm.ok_callback = function() {
			toastServices.show();
			orderServices.remove({
				delete_type: "1",
				orders_ids: course.orders_id
			}).then(function(data) {
				toastServices.hide()
				if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
					errorServices.autoHide(data.message);
					$timeout(function() {
						$route.reload();
					}, 2000)
				} else {
					errorServices.autoHide(data.message);
				}
			})
		}
	};
	$scope.pay = function() {
		var ids = $scope.courses.filter(function(c) {
			return c.selected
		}).map(function(course) {
			return course.orders_id;
		}).join("A");
		var url = $location.protocol() + "://" + $location.host() + ":" + $location.port() + "/#/payment?id=" + ids;
		$window.open(url);
	};
	$scope.remove_expired = function() {
		var expired_ids = $scope.courses.filter(function(c) {
			return c.status == '2';
		}).map(function(expired) {
			return expired.orders_id;
		}).join("#");
		toastServices.show();
		orderServices.remove({
			delete_type: "1",
			orders_ids: expired_ids
		}).then(function(data) {
			toastServices.hide()
			if (data.code == config.request.SUCCESS && data.status == config.response.SUCCESS) {
				errorServices.autoHide(data.message);
				$timeout(function() {
					$route.reload();
				}, 2000)
			} else {
				errorServices.autoHide(data.message);
			}
		})
	}
})